/* nav-toggler */
.header__nav-toggler {
  position: absolute;
  top: 21px;
  right: 16px;
  border: none;
  padding: 5px;
  background-color: transparent;
  cursor: pointer;
  height: 23px;
  display: none;
  transition: 0.3s;
}
@media (min-width: 660px) {
  .header__nav-toggler {
    top: 45px;
  }
}
.header__nav-line,
.header__nav-line::before,
.header__nav-line::after {
  width: 49px;
  height: 3px;
  background-color: #ffffff;
  display: block;
  transition: 0.3s;
}
.header__nav-toggler.header__toggler-open .header__nav-line {
  width: 25px;
}
.header__nav-toggler.header__toggler-open:hover .header__nav-line {
  background-color: transparent;
}
.header__nav-toggler:hover .header__nav-line,
.header__nav-toggler:hover .header__nav-line::before,
.header__nav-toggler:hover .header__nav-line::after {
  background-color: #d22856;
}
.header__nav-toggler.header__toggler-open:active .header__nav-line {
  background-color: transparent;
}
.header__nav-toggler:active .header__nav-line {
  opacity: 0.3;
}
.header__toggler-open .header__nav-line::before,
.header__toggler-open .header__nav-line::after {
  width: 30px;
}
.header__toggler-open {
  right: 16px;
}
.header__nav-line::before {
  content: "";
  transform: translateY(-9px);
}
.header__nav-line::after {
  content: "";
  transform: translateY(6px);
}
.header__toggler-open .header__nav-line {
  background-color: transparent;
}
.header__toggler-open .header__nav-line::before {
  transform: translateY(0) rotate(45deg);
}
.header__toggler-open .header__nav-line::after {
  transform: translateY(-3px) rotate(-45deg);
}
/* end nav-toggler*/
